import React, { useState, useEffect } from 'react'
import { navigate } from 'gatsby'
import axios from 'axios'

import Login from '../../components/common/Login'

import helpers from '../../helpers'

const { GET_ME } = helpers.api
const { linksPaths } = helpers.constants.links

function PrivateRouteComponent(props) {
    const { children } = props

    const [loggedIn, setLoggedIn] = useState(false)

    const isLoggedIn = () => axios.get(GET_ME, { withCredentials: true })
        .then(() => (!loggedIn ? setLoggedIn(true) : null))
        .catch(() => {
            if (loggedIn) {
                setLoggedIn(false)
            }
            navigate(linksPaths.root)
        })

    useEffect(() => {
        if (props.location.pathname !== linksPaths.root) {
            isLoggedIn()
        }
    })

    return !loggedIn ? <Login /> : children
}

export default PrivateRouteComponent
