import moment from 'moment'

const graphFormat = 'YYYY-MM-DD'

const graphTimes = [
    { key: '', text: '', value: JSON.stringify([moment().subtract(1, 'days').format(graphFormat), moment().format(graphFormat)]) },
    { key: '', text: '', value: JSON.stringify([moment().subtract(7, 'days').format(graphFormat), moment().format(graphFormat)]) },
    { key: '', text: '', value: JSON.stringify([moment().subtract(1, 'months').format(graphFormat), moment().format(graphFormat)]) },
    { key: '', text: '', value: JSON.stringify([moment().subtract(1, 'years').format(graphFormat), moment().format(graphFormat)]) },
]

export default {
    graphTimes,
}
